<template>
  <div id="params">
    <!--  -->
    <Crumbs :crumbs="this.crumbs"></Crumbs>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="warning" show-icon></el-alert>

      <!--  -->
      <p>
        
        <el-cascader
          v-model="cascaderList"
          :options="ParentList"
          :props="propConfig"
          @change="handleChange"
          clearable
        ></el-cascader>
      </p>

      <!--  -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane :disabled="isDisable" label="" name="many">
          <!--  -->
          <el-button type="primary" @click=" dialogVisibleParams = true"></el-button>
          <!--  -->
          <el-table :data="dynamicList" style="width: 100%" border>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- tag -->
                <el-tag
                  :key="i"
                  v-for="(tag,i) in scope.row.attr_vals"
                  closable
                  :disable-transitions="false"
                  @close="handleClose(i,scope.row)"
                >{{tag}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>

            <!--  -->
            <el-table-column label="#" type="index"></el-table-column>

            <el-table-column prop="attr_name" label=""></el-table-column>

            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="haandleEditAdd(scope.row.attr_id)"
                ></el-button>
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="removeParams(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>

        <el-tab-pane :disabled="isDisable" label="" name="only">
          <!--  -->
          <el-button type="primary" @click=" dialogVisibleParams = true"></el-button>

          <!--  -->
          <el-table :data="statiList" style="width: 100%" border>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- tag -->
                <el-tag
                  :key="i"
                  v-for="(tag,i) in scope.row.attr_vals"
                  closable
                  :disable-transitions="false"
                  @close="handleClose(i,scope.row)"
                >{{tag}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>

            <!--  -->
            <el-table-column label="#" type="index"></el-table-column>

            <el-table-column prop="attr_name" label=""></el-table-column>

            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="haandleEditAdd(scope.row.attr_id)"
                ></el-button>
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="removeParams(scope.row.attr_id)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!--  -->
    <el-dialog
      :title="''+this.paramsText"
      :visible.sync="dialogVisibleParams"
      width="50%"
      @close="handleCloseParams"
    >
      <el-form
        :model="ruleFormParams"
        :rules="rulesParams"
        ref="ruleParams"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item :label="this.paramsText" prop="attr_name">
          <el-input v-model="ruleFormParams.attr_name" wi></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleParams = false"> </el-button>
        <el-button type="primary" @click="handleAddParams"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="''+this.paramsText"
      :visible.sync="dialogVisibleParamsEdit"
      width="50%"
      @close="EdithandleCloseParams"
    >
      <el-form
        :model="ruleFormParamsEdit"
        :rules="rulesParamsEdit"
        ref="ruleParamsEdit"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item :label="this.paramsText" prop="attr_name">
          <el-input v-model="ruleFormParamsEdit.attr_name" wi></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleParamsEdit = false"> </el-button>
        <el-button type="primary" @click="handleAddParamsEdit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Crumbs from '../crumbs.vue'
export default {
  data() {
    return {
      // 
      crumbs: [
        {
          name: '',
          id: 0
        },
        {
          name: '',
          id: 1
        }
      ],
      // 
      cascaderList: [],
      // 
      propConfig: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      ParentList: [],
      // 
      activeName: 'many',
      // 
      dynamicList: [],
      // 
      statiList: [],
      // 
      dialogVisibleParams: false,
      // 
      ruleFormParams: {
        attr_name: ''
      },
      // 
      rulesParams: {
        attr_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ]
      },
      // 
      dialogVisibleParamsEdit: false,
      // 
      ruleFormParamsEdit: {
        attr_name: '',
        attr_id: null
      },
      // 
      rulesParamsEdit: {
        attr_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  components: {
    Crumbs
  },
  created() {
    this.getParamList()
  },
  computed: {
    // truefalse
    isDisable() {
      if (this.cascaderList.length !== 3) {
        return true
      }
      return false
    },
    // id
    sortId() {
      if (this.cascaderList.length !== 3) {
        return null
      }
      return this.cascaderList[2]
    },
    paramsText() {
      if (this.activeName === 'many') {
        return ''
      }
      return ''
    }
  },
  methods: {
    // 
    async getParamList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.ParentList = res.data
    },
    // 
    handleChange() {
      this.getSortParams()
    },
    // tab 
    async handleClick() {
      this.getSortParams()
    },

    // 
    async getSortParams() {
      // 
      if (this.cascaderList.length !== 3) {
        this.cascaderList = []
        this.dynamicList = []
        this.statiList = []
        return
      }

      const { data: res } = await this.$http.get(
        `categories/${this.sortId}/attributes`,
        {
          params: { sel: this.activeName }
        }
      )
      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 
        item.inputVisible = false
        // 
        item.inputValue = ''
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      if (this.activeName === 'many') {
        this.dynamicList = res.data
      } else {
        this.statiList = res.data
      }
    },

    // 
    handleCloseParams() {
      this.$refs.ruleParams.resetFields()
    },

    //  
    handleAddParams() {
      const confrim = this.$refs.ruleParams.validate(async (value) => {
        if (!value) return this.$message.error('')
        const { data: res } = await this.$http.post(
          `categories/${this.sortId}/attributes`,
          {
            attr_name: this.ruleFormParams.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 201) {
          this.dialogVisibleParams = false
          return this.$message.error('')
        }
        this.getSortParams()
        this.dialogVisibleParams = false
        this.$message.success('')
      })
    },

    // 
    EdithandleCloseParams() {
      this.$refs.ruleParamsEdit.resetFields()
    },

    // 
    handleAddParamsEdit() {
      this.$refs.ruleParamsEdit.validate(async (value) => {
        if (!value) {
          return this.$message.error('')
        }
        const { data: res } = await this.$http.put(
          `categories/${this.sortId}/attributes/${this.ruleFormParamsEdit.attr_id}`,
          {
            attr_name: this.ruleFormParamsEdit.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getSortParams()
        this.dialogVisibleParamsEdit = false
      })
    },

    // 
    async haandleEditAdd(cId) {
      this.dialogVisibleParamsEdit = true
      // id
      const { data: res } = await this.$http.get(
        `categories/${this.sortId}/attributes/${cId}`,
        {
          attr_sel: this.activeName
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.ruleFormParamsEdit.attr_name = res.data.attr_name
      this.ruleFormParamsEdit.attr_id = res.data.attr_id
      this.$message.success('')
    },

    // 
    async removeParams(id) {
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirm !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(
        `categories/${this.sortId}/attributes/${id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getSortParams()
    },

    // 
    showInput(row) {
      row.inputVisible = true
      // 
      // $nextTick 
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },

    // enter
    handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      // reture 
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      this.setAttrVals(row)
    },
    // attr_vals
    async setAttrVals(row) {
      const { data: res } = await this.$http.put(
        `categories/${this.sortId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(' ')
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
    },

    // 
    handleClose(i, row) {
      row.attr_vals.splice(i, 1)
      this.setAttrVals(row)
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 0 10px 0 0;
}
.el-input {
  width: 100px;
}
</style>
