<template>
  <div class="addItem">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/goods' }" id="item"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" show-icon :closable="false" center></el-alert>

      <!--  -->
      <el-steps :space="200" :active="Active" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!-- form -->

      <el-form
        :model="AddruleForm"
        :rules="Addrules"
        ref="AddruleForm"
        label-width="100px"
        class="demo-ruleForm"
        label-position="top"
      >
        <el-tabs tab-position="left" @tab-click="handleTab" :before-leave="leaveTab">
          <el-tab-pane label="">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="AddruleForm.goods_name"></el-input>
            </el-form-item>

            <el-form-item label="()" prop="goods_price">
              <el-input v-model="AddruleForm.goods_price" type="number"></el-input>
            </el-form-item>

            <el-form-item label="(kg)" prop="goods_weight">
              <el-input v-model="AddruleForm.goods_weight" type="number"></el-input>
            </el-form-item>

            <el-form-item label="" prop="goods_number">
              <el-input v-model="AddruleForm.goods_number" type="number"></el-input>
            </el-form-item>

            <el-form-item label="" prop="goods_cat">
              <!--  -->
              <el-cascader
                v-model="AddruleForm.goods_cat"
                :options="CascaderList"
                :props="propConfig"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="">
            <el-form-item :label="item.attr_name" v-for="(item,i) in manyList" :key="i">
              <el-checkbox-group v-model="item.attr_vals" size="small">
                <el-checkbox :label="item" v-for="(item,i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="">
            <el-form-item :label="item.attr_name" v-for="(item,i) in onlyList" :key="i">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="">
            <el-upload
              class="upload-demo"
              :action="picURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              :headers="headerUrl"
              list-type="picture"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="">
            <!--  -->

            <quill-editor style="height:200px" v-model="AddruleForm.goods_introduce"></quill-editor>

            <!--  -->

            <el-button type="primary" style="margin-top:80px" @click="addCommodity"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisiblePreview" width="50%">
      <img :src="previewImg" style="width:100%" />
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data() {
    return {
      // 
      Active: 0,

      // 
      AddruleForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        goods_cat: [],
        pics: [],
        goods_introduce: '',
        attrs: []
      },

      // 
      Addrules: {
        // 
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        // 
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        // 
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        // 
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        // 
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },

      // 
      CascaderList: [],
      // 
      propConfig: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },

      // 
      manyList: [],

      // 
      onlyList: [],

      // 
      headerUrl: {
        Authorization: window.sessionStorage.getItem('token')
      },

      // 
      picURL: 'http://server.sineava.top/api/private/v1/upload',

      // 
      previewImg: '',

      // 
      dialogVisiblePreview: false
    }
  },
  computed: {
    sortId() {
      if (this.AddruleForm.goods_cat.length !== 3) {
        return null
      }
      return this.AddruleForm.goods_cat[2]
    }
  },
  created() {
    this.getCascader()
  },
  methods: {
    // tab
    async handleTab(e) {
      this.Active = Number(e.index)
      // 
      if (this.Active === 1) {
        const { data: res } = await this.$http.get(
          `categories/${this.sortId}/attributes`,
          {
            params: { sel: 'many' }
          }
        )
        res.data.forEach((item) => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        })
        this.manyList = res.data
        // 
      } else if (this.Active === 2) {
        const { data: res } = await this.$http.get(
          `categories/${this.sortId}/attributes`,
          {
            params: { sel: 'only' }
          }
        )
        this.onlyList = res.data
      }
    },
    // 
    async getCascader() {
      const { data: res } = await this.$http.get('categories')
      this.CascaderList = res.data
    },

    // 
    handleChange() {
      if (this.AddruleForm.goods_cat.length !== 3) {
        return (this.AddruleForm.goods_cat = [])
      }
    },

    // tab
    leaveTab(activeName, oldActiveName) {
      if (oldActiveName === '0' && this.AddruleForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },

    // 
    handlePreview(file) {
      this.previewImg = file.response.data.url
      this.dialogVisiblePreview = true
    },

    // 
    handleRemove(file) {
      // 1
      const removeImg = file.response.data.tmp_path

      // 2pics
      const i = this.AddruleForm.pics.findIndex((i) => i.pic === removeImg)

      // 3pics splice
      this.AddruleForm.pics.splice(i, 1)
    },

    // 
    handleSuccess(response) {
      // 1
      const picInfo = {
        pic: response.data.tmp_path
      }
      // 2pushpics
      this.AddruleForm.pics.push(picInfo)
    },

    // 
    addCommodity() {
      this.$refs.AddruleForm.validate(async (value) => {
        if (!value) {
          return this.$message.error('')
        }

        // 

        // 1 AddruleForm
        const froms = _.cloneDeep(this.AddruleForm)
        froms.goods_cat = froms.goods_cat.join(',')

        // 
        this.manyList.forEach((item) => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          }
          this.AddruleForm.attrs.push(newInfo)
        })

        // 
        this.onlyList.forEach((item) => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.AddruleForm.attrs.push(newInfo)
        })
        froms.attrs = this.AddruleForm.attrs

        // 
        // 
        const { data: res } = await this.$http.post('goods', froms)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success(res.meta.msg)
        this.$router.push('/goods')
      })
    }
  }
}
</script>

<style lang="less" scoped>
#item {
  .is-link {
    font-weight: 400 !important;
  }
}
.el-breadcrumb {
  font-size: 17px;
}
.el-alert {
  background-color: #f4f4f5 !important;
  height: 40px;
  margin: 10px 0 30px 0;
}
.el-tabs {
  margin-top: 30px;
}
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
</style>
