<template>
  <div id="categories">
    <!--  -->
    <Crumbs :crumbs="this.crumbs"></Crumbs>

    <!--  -->
    <el-card>
      <!--  -->
      <el-button style="margin-bottom:20px;" type="primary" @click="handleSort"></el-button>

      <!--  -->
      <tree-table
        index-text="#"
        show-index
        :expand-type="false"
        :data="categoriesList"
        :columns="columns"
        :selection-type="false"
        border
      >
        <!--  -->
        <template slot="isOk" slot-scope="scope">
          <i v-if="scope.row.cat_deleted==='false'" class="el-icon-error"></i>
          <i v-else class="el-icon-success"></i>
        </template>
        <!--  -->
        <template slot="sort" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag size="mini" type="success" v-if="scope.row.cat_level === 1"></el-tag>
          <el-tag size="mini" type="warning" v-if="scope.row.cat_level === 2"></el-tag>
        </template>
        <!--  -->
        <template slot="operation" slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            icon="el-icon-edit"
            @click="handleEdit(scope.row)"
          ></el-button>
          <el-button
            size="mini"
            type="danger"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
          ></el-button>
        </template>
      </tree-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="parameter.pagenum"
        :page-sizes="[3, 5, 7, 10]"
        :page-size="parameter.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="categoriesTotal"
      ></el-pagination>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="dialogVisibleSort"
        width="50%"
        @close="handleCloseSort"
      >
        <!--  -->
        <el-form
          class="demo-ruleForm"
          :model="ruleFormSort"
          :rules="ruleSort"
          ref="ruleForm"
          label-width="100px"
        >
          <el-form-item label="" prop="cat_name">
            <el-input v-model="ruleFormSort.cat_name"></el-input>
          </el-form-item>
          <el-form-item label="" id="from">
            <!-- options -->
            <!-- props -->
            <el-cascader
              v-model="cascaderList"
              :options="ParentList"
              :props="propConfig"
              @change="handleChange"
              clearable
              change-on-select
            ></el-cascader>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisibleSort = false"> </el-button>
          <el-button type="primary" @click="handleAddSort"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisibleEdit" width="50%">
        <el-form
          class="demo-ruleForm"
          :model="editFrom"
          :rules="ruleSort"
          ref="ruleForm"
          label-width="100px"
        >
          <el-form-item label="" prop="cat_name">
            <el-input v-model="editFrom.cat_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisibleEdit = false"> </el-button>
          <el-button type="primary" @click="determineEdit"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import Crumbs from '../crumbs.vue'
export default {
  data() {
    return {
      // 
      crumbs: [
        {
          name: '',
          id: 0
        },
        {
          name: '',
          id: 1
        }
      ],
      // 
      categoriesList: [],
      // 
      parameter: {
        type: 3, // 
        pagenum: 1, // 
        pagesize: 5 // 
      },
      // 
      categoriesTotal: 0,
      // 
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          prop: 'cat_deleted',
          type: 'template',
          template: 'isOk',
          align: 'center',
          headerAlign: 'center'
        },
        {
          label: '',
          prop: 'cat_level',
          type: 'template',
          template: 'sort',
          align: 'center',
          headerAlign: 'center'
        },
        {
          label: '',
          type: 'template',
          template: 'operation',
          align: 'center',
          headerAlign: 'center'
        }
      ],
      // 
      dialogVisibleSort: false,
      // 
      ruleFormSort: {
        // 
        cat_name: '',
        // id
        cat_pid: 0,
        // 
        cat_level: 0
      },
      // 
      ruleSort: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 2,
            max: 6,
            message: ' 2  6 ',
            trigger: 'blur'
          }
        ]
      },
      // 
      ParentList: [],
      // 
      cascaderList: [],
      // 
      propConfig: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      dialogVisibleEdit: false,
      // 
      editFrom: {
        cat_name: ''
      },
      // id
      editId: null
    }
  },
  components: {
    Crumbs
  },
  created() {
    this.getCategories()
  },
  methods: {
    // 
    async getCategories() {
      const { data: res } = await this.$http.get('categories', {
        params: this.parameter
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //  
      this.categoriesList = res.data.result
      this.categoriesTotal = res.data.total
      this.$message.success('')
    },

    // pagesize 
    handleSizeChange(newSize) {
      this.parameter.pagesize = newSize
      this.getCategories()
    },

    // pagenum 
    handleCurrentChange(newPagenum) {
      this.parameter.pagenum = newPagenum
      this.getCategories()
    },

    // 
    handleCloseSort() {
      this.$refs.ruleForm.resetFields()
      this.cascaderList = []
      this.ruleFormSort.cat_pid = 0
      this.ruleFormSort.cat_level = 0
    },

    // 
    handleSort() {
      this.getParamsList()
      this.dialogVisibleSort = true
    },

    // 
    async getParamsList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.ParentList = res.data
      this.$message.success('')
    },

    // 
    handleChange() {
      // cascaderListlength0
      // 
      if (this.cascaderList.length > 0) {
        // id
        this.ruleFormSort.cat_pid = this.cascaderList[
          this.cascaderList.length - 1
        ]
        // 
        this.ruleFormSort.cat_level = this.cascaderList.length
      } else {
        // id
        this.ruleFormSort.cat_pid = 0
        // 
        this.ruleFormSort.cat_level = 0
      }
    },

    // 
    handleAddSort() {
      this.$refs.ruleForm.validate(async (value) => {
        if (!value) {
          return this.$message.info('')
        }
        const { data: res } = await this.$http.post(
          'categories',
          this.ruleFormSort
        )
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        let text = ''
        if (res.data.cat_level === 0) {
          text = ''
        } else if (res.data.cat_level === 1) {
          text = ''
        } else if (res.data.cat_level === 2) {
          text = ''
        }
        this.$message.success(`${text}`)
        this.getCategories()
        this.dialogVisibleSort = false
      })
    },

    // id
    async handleEdit(scope) {
      this.editId = scope.cat_id
      this.dialogVisibleEdit = true
      const { data: res } = await this.$http.get(`categories/${scope.cat_id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.editFrom.cat_name = res.data.cat_name
    },
    async determineEdit() {
      const { data: res1 } = await this.$http.put(
        `categories/${this.editId}`,
        this.editFrom
      )
      if (res1.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getCategories()
      this.dialogVisibleEdit = false
    },
    // 
    async handleDelete(scope) {
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirm !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(
        `categories/${scope.cat_id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success(res.meta.msg)
      this.getCategories()
    }
  }
}
</script>

<style lang="less" scoped>
.el-icon-error {
  color: red;
}
.el-icon-success {
  color: #20b2aa;
}
.el-cascader {
  width: 100%;
}
</style>
