<template>
  <div id="goods">
    <!--  -->
    <Crumbs :crumbs="this.crumbs"></Crumbs>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="8">
          <el-input placeholder="" v-model="paramsInfo.query" clearable>
            <el-button slot="append" icon="el-icon-search" @click="GetList"></el-button>
          </el-input>
        </el-col>
        <!--  -->
        <el-col :span="4">
          <el-button type="primary" @click="handlePages"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="ListData" stripe border style="width: 100%">
        <el-table-column type="index" label="#" width="50px"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="()" width="110px"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="80px"></el-table-column>
        <el-table-column label="" width="170px">
          <template slot-scope="scope">{{scope.row.add_time | dateFormat}}</template>
        </el-table-column>
        <el-table-column label="" width="160px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" @click="handleEdit()"></el-button>
            <el-button type="danger" icon="el-icon-delete" @click="removeUser(scope.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="paramsInfo.pagenum"
        :page-sizes="[10, 15, 20, 30]"
        :page-size="paramsInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totals"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
import Crumbs from '../crumbs.vue'
export default {
  data() {
    return {
      // 
      crumbs: [
        {
          name: '',
          id: 0
        },
        {
          name: '',
          id: 1
        }
      ],

      // 
      paramsInfo: {
        // 
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 10
      },

      // 
      totals: null,

      // 
      ListData: []
    }
  },
  components: {
    Crumbs
  },
  created() {
    this.GetList()
  },
  methods: {
    // 
    async GetList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.paramsInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.ListData = res.data.goods
      this.totals = res.data.total
      this.$message.success('')
    },

    //  pagesize  
    handleSizeChange(newPagesize) {
      this.paramsInfo.pagesize = newPagesize
      this.GetList()
    },

    //  pagenum   
    handleCurrentChange(newPagesize) {
      this.paramsInfo.pagenum = newPagesize
      this.GetList()
    },

    // 
    handleEdit() {},

    // id
    async removeUser(id) {
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirm !== 'confirm') {
        return this.$message.info('')
      }
      // 
      const { data: res } = await this.$http.delete(`goods/${id}`)
      if (res.meta.status !== 200) {
        this.$message.error('')
      }
      this.$message.success('')
      this.GetList()
    },

    // 
    handlePages() {
      this.$router.push({ path: '/addItem' })
    }
  }
}
</script>

<style lang="less" scoped>
</style>
